body{
    margin:0;
}

div{
    box-sizing: border-box;
}

$maincolor:#ff0000;



.red {
    color: $maincolor;
}


.my-alert {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert-red{
    @extend .my-alert;
    background-color: aqua;
}
